.App {
  display: flex;
  justify-content: space-around;
  width: 90vw;
  margin: auto; }

.btn {
  cursor: pointer;
  color: #ffffff;
  text-decoration: none;
  padding: 2px 6px;
  border-radius: 6px;
  font-size: 24px;
  border: none;
  margin-left: 5px; }

.btn-green {
  background: #2ecc71; }
  .btn-green:hover {
    background: #26ab5f;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease; }
  .btn-green:active {
    background: #176437; }

.btn-orange {
  background: #f7b11a; }
  .btn-orange:hover {
    background: #e09c08;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease; }
  .btn-orange:active {
    background: #8d6205; }

.btn-red {
  background: #e74c3c; }
  .btn-red:hover {
    background: #df2e1b;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease; }
  .btn-red:active {
    background: #921e12; }

.btn-inverted {
  background: #e2e2e2;
  font-size: 16px;
  border: 1px solid #e74c3c;
  color: #e74c3c; }
  .btn-inverted:hover {
    background: #cecece;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease; }
  .btn-inverted:active {
    background: #a2a2a2; }

